import { Component, OnInit, ChangeDetectorRef } from '@angular/core';
import { FormArray, FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { AdminService } from '../../../services/admin.service';
import { MessagingService } from '../../../services/messaging.service';
import { UserPreferenceListItem, UserPreferenceDetail } from '../../../models/admin.model';
import { NzModalService } from 'ng-zorro-antd/modal';

@Component({
  selector: 'app-admin-user-preferences',
  templateUrl: './admin-user-preferences.component.html',
  styleUrls: ['./admin-user-preferences.component.scss']
})
export class AdminUserPreferencesComponent implements OnInit {
  loading = false;
  loadingDetail = false;
  error: string | null = null;

  users: UserPreferenceListItem[] = [];
  filteredUsers: UserPreferenceListItem[] = [];
  searchText = '';

  // Modal state
  isModalVisible = false;
  selectedUser: UserPreferenceDetail | null = null;

  // Preferences edit state
  viewMode: 'form' | 'raw' = 'form';
  preferencesForm: FormGroup | null = null;
  rawPreferencesText = '';

  // Available application tabs that can be enabled/disabled for users
  // Keep this in sync with actual routes/features in the app
  availableTabs: string[] = [
    'prior-auth',
    'reporting',
    'oral-chemotherapy',
    'admin',
    'feedback'
  ];

  constructor(
    private adminService: AdminService,
    private messagingService: MessagingService,
    private cdr: ChangeDetectorRef,
    private modal: NzModalService
  ) {}

  ngOnInit(): void {
    this.loadUsers();
  }

  /**
   * Load all users from the backend
   */
  loadUsers(): void {
    this.loading = true;
    this.error = null;

    this.adminService.getAllUsers().subscribe({
      next: (data: any) => {
        if (data && data.users) {
          this.users = data.users.map((user: any) => ({
            userId: user.user_id || user.userId,
            username: user.username,
            fullName: user.full_name || user.fullName,
            position: user.position,
            hasPreferences: user.has_preferences || user.hasPreferences || false,
            enabled: user.enabled !== undefined ? (user.enabled === 1 || user.enabled === true) : true
          }));
          this.filteredUsers = [...this.users];
        } else {
          this.users = [];
          this.filteredUsers = [];
        }
        this.loading = false;
        this.cdr.detectChanges();
      },
      error: (err: unknown) => {
        this.error = 'Failed to load users';
        this.messagingService.error('Failed to load users');
        this.loading = false;
        this.cdr.detectChanges();
      }
    });
  }

  /**
   * Filter users based on search text
   */
  onSearch(): void {
    const searchLower = this.searchText.toLowerCase().trim();

    if (!searchLower) {
      this.filteredUsers = [...this.users];
    } else {
      this.filteredUsers = this.users.filter(user =>
        user.username.toLowerCase().includes(searchLower) ||
        user.fullName.toLowerCase().includes(searchLower) ||
        user.position.toLowerCase().includes(searchLower)
      );
    }
    this.cdr.detectChanges();
  }

  /**
   * View user preferences detail
   */
  viewUserPreferences(user: UserPreferenceListItem): void {
    this.loadingDetail = true;
    this.selectedUser = null;

    this.adminService.getUserPreferencesById(user.userId).subscribe({
      next: (data: any) => {
        const apiEnabledRaw = (data as any).enabled;
        const resolvedEnabled = apiEnabledRaw !== undefined ? this.coerceEnabled(apiEnabledRaw) : user.enabled;

        // Normalize preferences keys from API (supports snake_case and camelCase)
        const ds = (data.preferences?.display_settings || data.preferences?.displaySettings || {}) as any;
        this.selectedUser = {
          curdomain: data.curdomain || '',
          curnode: data.curnode || '',
          userId: data.user_id || data.userId,
          username: data.username,
          fullName: data.full_name || data.fullName,
          position: data.position,
          enabled: resolvedEnabled,
          hasPreferences: data.has_preferences || data.hasPreferences || false,
          runDtTm: data.runDtTm || data.run_dt_tm || '',
          performanceSettings: data.performanceSettings || data.performance_settings || { batchMode: 0 },
          preferences: {
            accessConfig: data.preferences?.accessConfig || data.preferences?.access_config || '',
            saved: data.preferences?.saved || '',
            columns: data.preferences?.columns || '',
            filters: data.preferences?.filters || '',
            customWorklists: data.preferences?.customWorklists,
            // Map display settings with tolerance for both snake_case and camelCase
            displaySettings: {
              enabledTabs: ds.enabled_tabs || ds.enabledTabs || [],
              defaultTab: ds.default_tab || ds.defaultTab || '',
              readonlyTabs: ds.readonly_tabs || ds.readonlyTabs || []
            }
          },
          autoTextCnt: data.autoTextCnt || data.auto_text_cnt || 0,
          autoText: data.autoText || data.auto_text || []
        };
        // Reset editor mode each time a user is opened
        this.viewMode = 'form';
        this.initializePreferencesEditor();
        this.isModalVisible = true;
        this.loadingDetail = false;
        this.cdr.detectChanges();
      },
      error: (err: unknown) => {
        this.messagingService.error('Failed to load user preferences');
        this.loadingDetail = false;
        this.cdr.detectChanges();
      }
    });
  }

  /**
   * Toggle user enabled state
   */
  toggleUserEnabled(user: UserPreferenceListItem, enabled: boolean): void {
    // Optimistically update the UI
    const originalEnabled = user.enabled;
    user.enabled = enabled;

    this.adminService.setUserEnabled({ username: user.username, enabled }).subscribe({
      next: () => {
        this.messagingService.success(
          `${user.username} has been ${enabled ? 'enabled' : 'disabled'}`
        );
        // Update in filtered users as well
        const filteredUser = this.filteredUsers.find(u => u.userId === user.userId);
        if (filteredUser) {
          filteredUser.enabled = enabled;
        }
        this.cdr.detectChanges();
      },
      error: (err: unknown) => {
        // Revert optimistic update
        user.enabled = originalEnabled;
        const filteredUser = this.filteredUsers.find(u => u.userId === user.userId);
        if (filteredUser) {
          filteredUser.enabled = originalEnabled;
        }
        this.messagingService.error(`Failed to ${enabled ? 'enable' : 'disable'} user`);
        this.cdr.detectChanges();
      }
    });
  }

  /**
   * Toggle user enabled state in modal
   */
  toggleUserEnabledInModal(enabled: boolean): void {
    if (!this.selectedUser) {
      return;
    }

    const originalEnabled = this.selectedUser.enabled;
    this.selectedUser.enabled = enabled;

    this.adminService.setUserEnabled({ username: this.selectedUser.username, enabled }).subscribe({
      next: () => {
        // Update the user in the main list as well
        const user = this.users.find(u => u.userId === this.selectedUser!.userId);
        if (user) {
          user.enabled = enabled;
        }
        const filteredUser = this.filteredUsers.find(u => u.userId === this.selectedUser!.userId);
        if (filteredUser) {
          filteredUser.enabled = enabled;
        }
        this.messagingService.success(
          `${this.selectedUser!.username} has been ${enabled ? 'enabled' : 'disabled'}`
        );
        this.cdr.detectChanges();
      },
      error: (err: unknown) => {
        // Revert optimistic update
        if (this.selectedUser) {
          this.selectedUser.enabled = originalEnabled;
        }
        this.messagingService.error(`Failed to ${enabled ? 'enable' : 'disable'} user`);
        this.cdr.detectChanges();
      }
    });
  }

  /**
   * Close the modal
   */
  handleModalClose(): void {
    this.isModalVisible = false;
    this.selectedUser = null;
    this.preferencesForm = null;
    this.rawPreferencesText = '';
    this.cdr.detectChanges();
  }

  /**
   * Format array for display
   */
  formatArray(arr: string[]): string {
    return arr && arr.length > 0 ? arr.join(', ') : 'None';
  }

  /**
   * Normalize enabled value from various possible types
   */
  private coerceEnabled(value: unknown): boolean {
    if (typeof value === 'number') {
      return value === 1;
    }
    if (typeof value === 'string') {
      const v = value.toLowerCase();
      return v === '1' || v === 'true' || v === 'y' || v === 'yes' || v === 'enabled';
    }
    return value === true;
  }

  /**
   * Initialize editor state (form + raw) from saved preferences JSON
   */
  private initializePreferencesEditor(): void {
    // Build an effective preferences object from either the saved JSON string
    // or from direct fields (columns/filters/customWorklists) if present.
    const savedValue: any = (this.selectedUser as any)?.preferences?.saved;
    let parsed: any | null = null;
    if (savedValue) {
      if (typeof savedValue === 'string') {
        try {
          parsed = JSON.parse(savedValue);
        } catch {
          parsed = null;
        }
      } else if (typeof savedValue === 'object') {
        parsed = savedValue;
      }
    }
    if (!parsed) {
      parsed = {
        columns: this.selectedUser?.preferences.columns || {},
        filters: this.selectedUser?.preferences.filters || {},
        customWorklists: this.selectedUser?.preferences.customWorklists || []
      };
    }

    // Remove displaySettings from saved JSON if it exists (should only be at top level)
    if (parsed.displaySettings) {
      delete parsed.displaySettings;
    }

    // Keep a pretty JSON in the raw editor (without displaySettings)
    this.rawPreferencesText = JSON.stringify(parsed, null, 2);

    const customWorklists = Array.isArray(parsed.customWorklists) ? parsed.customWorklists : [];
    // Get displaySettings only from top-level preferences, not from saved JSON
    const displaySettings = this.selectedUser?.preferences.displaySettings || {
      enabledTabs: [],
      defaultTab: '',
      readonlyTabs: []
    };

    // Build the reactive form including display settings
    this.preferencesForm = new FormGroup({
      columns: new FormControl(parsed.columns || {}),
      filters: new FormControl(parsed.filters || {}),
      customWorklists: new FormArray(
        customWorklists.map((wl: any) => this.createWorklistGroup(wl))
      ),
      displaySettings: new FormGroup({
        enabledTabs: new FormControl(displaySettings.enabledTabs || []),
        defaultTab: new FormControl(displaySettings.defaultTab || ''),
        readonlyTabs: new FormControl(displaySettings.readonlyTabs || [])
      })
    });
  }

  get customWorklistsArray(): FormArray | null {
    return (this.preferencesForm?.get('customWorklists') as FormArray) || null;
  }

  private createWorklistGroup(wl?: any): FormGroup {
    return new FormGroup({
      id: new FormControl(wl?.id || ''),
      name: new FormControl(wl?.name || '', [Validators.required]),
      route: new FormControl(wl?.route || 'prior-auth', [Validators.required]),
      subroute: new FormControl(wl?.subroute || 'incoming', [Validators.required]),
      filters: new FormControl(wl?.filters || {}),
      isDefault: new FormControl(Boolean(wl?.isDefault) || false),
      createdDate: new FormControl(wl?.createdDate || new Date().toISOString()),
      modifiedDate: new FormControl(wl?.modifiedDate || new Date().toISOString())
    });
  }

  addWorklist(): void {
    if (!this.customWorklistsArray) { return; }
    const group = this.createWorklistGroup({ id: `worklist-${Date.now()}` });
    this.customWorklistsArray.push(group);
    this.touchModified();
  }

  removeWorklist(index: number): void {
    if (!this.customWorklistsArray) { return; }
    this.customWorklistsArray.removeAt(index);
    this.touchModified();
  }

  touchModified(): void {
    if (!this.customWorklistsArray) { return; }
    const now = new Date().toISOString();
    this.customWorklistsArray.controls.forEach(ctrl => {
      const fg = ctrl as FormGroup;
      if (fg.get('modifiedDate')) {
        fg.get('modifiedDate')!.setValue(now, { emitEvent: false });
      }
    });
  }

  /**
   * Persist preferences for the selected user
   */
  savePreferences(): void {
    if (!this.selectedUser) { return; }

    // Build saved JSON (without displaySettings - that goes separately at top level)
    let savedData: any = {};
    let displaySettings: any = {};

    if (this.viewMode === 'raw') {
      // Validate JSON
      try {
        savedData = JSON.parse(this.rawPreferencesText || '{}');
        // Remove displaySettings if it exists in raw JSON (should not be there)
        if (savedData.displaySettings) {
          delete savedData.displaySettings;
        }
        // Get displaySettings from form if available, otherwise from selectedUser
        if (this.preferencesForm) {
          const formValue = this.preferencesForm.value;
          displaySettings = {
            enabledTabs: formValue.displaySettings?.enabledTabs || [],
            defaultTab: formValue.displaySettings?.defaultTab || '',
            readonlyTabs: formValue.displaySettings?.readonlyTabs || []
          };
        } else {
          displaySettings = this.selectedUser.preferences.displaySettings || {
            enabledTabs: [],
            defaultTab: '',
            readonlyTabs: []
          };
        }
      } catch {
        this.messagingService.error('Invalid JSON. Please fix errors in Raw JSON.');
        return;
      }
    } else if (this.preferencesForm) {
      const formValue = this.preferencesForm.value;
      // Build saved data without displaySettings
      savedData = {
        columns: formValue.columns || {},
        filters: formValue.filters || {},
        customWorklists: formValue.customWorklists || []
      };
      // Validate defaultTab is included in enabledTabs; if not, coerce
      const enabledTabs: string[] = formValue.displaySettings?.enabledTabs || [];
      let defaultTab: string = formValue.displaySettings?.defaultTab || '';
      if (defaultTab && !enabledTabs.includes(defaultTab)) {
        defaultTab = enabledTabs.length > 0 ? enabledTabs[0] : '';
      }
      displaySettings = {
        enabledTabs: enabledTabs,
        defaultTab: defaultTab,
        readonlyTabs: formValue.displaySettings?.readonlyTabs || []
      };
    }

    // Send preferences data directly (columns, filters, customWorklists)
    // The backend will store this as the "saved" preferences JSON
    // displaySettings are handled separately through user access config
    const payload = savedData;

    // Save preferences and display settings
    // First save the preferences data
    this.adminService.saveUserPreferencesForUser(this.selectedUser.userId, payload).subscribe({
      next: () => {
        // Always save display settings (enabled tabs and default tab), even if empty
        // This allows setting tabs when none are currently enabled
        this.adminService.setUserDisplaySettings({
          username: this.selectedUser!.username,
          enabledTabs: displaySettings.enabledTabs || [],
          defaultTab: displaySettings.defaultTab || ''
        }).subscribe({
          next: () => {
            this.messagingService.success('Preferences and display settings saved');
            // Update local copy and mark as having preferences
            this.selectedUser!.preferences.saved = JSON.stringify(payload);
            this.selectedUser!.preferences.displaySettings = displaySettings;
            this.selectedUser!.hasPreferences = true;
            this.cdr.detectChanges();
          },
          error: () => {
            // Preferences saved but display settings failed
            this.messagingService.warning('Preferences saved but failed to save display settings');
            this.selectedUser!.preferences.saved = JSON.stringify(payload);
            this.cdr.detectChanges();
          }
        });
      },
      error: () => {
        this.messagingService.error('Failed to save preferences');
        this.cdr.detectChanges();
      }
    });
  }

  onViewModeChange(mode: 'form' | 'raw'): void {
    // Keep editors in sync when toggling
    if (mode === 'raw' && this.preferencesForm) {
      const formValue = this.preferencesForm.value;
      // Raw JSON should not include displaySettings (that's stored at top level)
      const snapshot = {
        columns: formValue.columns || {},
        filters: formValue.filters || {},
        customWorklists: formValue.customWorklists || []
      };
      this.rawPreferencesText = JSON.stringify(snapshot, null, 2);
    }
    if (mode === 'form') {
      try {
        const parsed = JSON.parse(this.rawPreferencesText || '{}');
        // Remove displaySettings from parsed JSON if it exists (should not be there)
        if (parsed.displaySettings) {
          delete parsed.displaySettings;
        }
        const customWorklists = Array.isArray(parsed.customWorklists) ? parsed.customWorklists : [];
        // Get displaySettings only from top-level preferences, not from saved JSON
        const displaySettings = this.selectedUser?.preferences.displaySettings || {
          enabledTabs: [],
          defaultTab: '',
          readonlyTabs: []
        };
        this.preferencesForm = new FormGroup({
          columns: new FormControl(parsed.columns || {}),
          filters: new FormControl(parsed.filters || {}),
          customWorklists: new FormArray(
            customWorklists.map((wl: any) => this.createWorklistGroup(wl))
          ),
          displaySettings: new FormGroup({
            enabledTabs: new FormControl(displaySettings.enabledTabs || []),
            defaultTab: new FormControl(displaySettings.defaultTab || ''),
            readonlyTabs: new FormControl(displaySettings.readonlyTabs || [])
          })
        });
      } catch {
        // Stay in raw mode if invalid JSON
        this.messagingService.error('Invalid JSON; cannot switch to Form view until fixed.');
        this.viewMode = 'raw';
        this.cdr.detectChanges();
        return;
      }
    }
    this.viewMode = mode;
    this.cdr.detectChanges();
  }
}

