import { Component, Output, EventEmitter, OnInit, effect, signal, computed, NgZone, ChangeDetectorRef } from '@angular/core';
import { SidebarStateService } from '../../../services/sidebar-state.service';
import { UserPreferencesService } from '../../../services/user-preferences.service';
import { ColumnConfigService } from '../../../services/column-config.service';
import { PriorAuthService } from '../../../services/prior-auth.service';
import { CustomWorklistService } from '../../../services/custom-worklist.service';
import { CernerActionService } from '../../../services/cerner-action.service';
import { PredefinedFilter } from '../../../models/predefined-filter.model';
import { CustomWorklist } from '../../../models/custom-worklist.model';
import { PersonService } from '@clinicaloffice/clinical-office-mpage-core';

@Component({
  selector: 'app-prior-auth-filter-bar',
  templateUrl: './prior-auth-filter-bar.component.html',
  styleUrls: ['./prior-auth-filter-bar.component.scss']
})
export class PriorAuthFilterBarComponent implements OnInit {
  @Output() columnWidthsReset = new EventEmitter<void>();
  @Output() daysFilterChange = new EventEmitter<number | 'all'>();
  @Output() refreshData = new EventEmitter<void>();
  @Output() clearData = new EventEmitter<void>();
  /**
   * Emits the global search string to the parent component for filtering.
   */
  @Output() globalSearch = new EventEmitter<string>();
  /**
   * Emits when a predefined filter tab is selected
   */
  @Output() predefinedFilterSelect = new EventEmitter<PredefinedFilter | null>();
  /**
   * Emits when column filters should be cleared
   */
  @Output() clearColumnFilters = new EventEmitter<void>();
  /**
   * Emits when the assignment filter toggle is changed
   */
  @Output() assignmentToggle = new EventEmitter<boolean>();
  /**
   * Emits when the wizard view should be shown
   */
  @Output() showWizardView = new EventEmitter<{ editWorklist: CustomWorklist | null; subroute: string }>();
  /**
   * Emits when a patient is selected from the patient search dialog
   */
  @Output() patientSelected = new EventEmitter<{ personId: number; encounterId: number }>();
  /**
   * Emits when the patient filter should be cleared
   */
  @Output() clearPatientFilter = new EventEmitter<void>();

  // Loading state from PriorAuthService using signals
  get isLoading() {
    return this.priorAuthService.loading();
  }
  
  // Processing count from PriorAuthService using signals
  get processingCount() {
    return this.priorAuthService.processingCount();
  }

  // Filter counts from PriorAuthService using signals
  get filterCounts() {
    return this.priorAuthService.predefinedFilterCounts();
  }

  /**
   * Gets the count for a specific predefined filter
   */
  getFilterCount(filterId: string): number {
    const count = this.filterCounts[filterId] || 0;
    return count;
  }

  /**
   * Gets the tab title with badge count for a filter
   */
  getTabTitle(filter: PredefinedFilter): string {
    const count = this.getFilterCount(filter.id);
    return count > 0 ? `${filter.label} (${count})` : filter.label;
  }

  /**
   * Check if test order button should be visible (not in P0665 domain and curdomain is defined)
   */
  get showTestOrderButton(): boolean {
    const userData = this.userPreferences.getUserData();
    const curdomain = userData?.curdomain;
    return curdomain !== undefined && curdomain !== null && curdomain !== 'P0665';
  }

  /**
   * Create test orders using CCL script
   */
  onCreateTestOrders(): void {
    this.priorAuthService.createTestOrders().subscribe({
      next: (response) => {
        if (response.success) {
          // Test orders created successfully - wait 2 seconds before refreshing to allow system time to create the order
          setTimeout(() => {
            this.refreshData.emit(); // Refresh the data to show new test orders
          }, 2000);
        }
      },
      error: (error) => {
        // Error creating test orders
      }
    });
  }

  /**
   * Selected patient information from patient search
   */
  selectedPatient: { personId: number; encounterId: number } | null = null;

  /**
   * Signal tracking the selected person ID for reactive updates
   */
  private _selectedPersonId = signal<number | null>(null);

  /**
   * Signal for selected patient name that updates reactively when PersonService loads data
   */
  selectedPatientName = signal<string>('');

  constructor(
    public sidebarState: SidebarStateService,
    public userPreferences: UserPreferencesService,
    private columnConfig: ColumnConfigService,
    private priorAuthService: PriorAuthService,
    public customWorklistService: CustomWorklistService,
    private cernerActionService: CernerActionService,
    private personService: PersonService,
    private ngZone: NgZone,
    private cdr: ChangeDetectorRef
  ) {
    // Effect to handle configuration loading and set default filter
    effect(() => {
      const configLoaded = this.columnConfig.configurationLoaded();
      if (configLoaded && !this.selectedPredefinedFilter && !this.selectedCustomWorklist) {
        this.initializeDefaultFilter();
      }
    });

    // Effect to reactively update patient name when PersonService loads the data
    effect(() => {
      const personId = this._selectedPersonId();
      if (!personId) {
        this.ngZone.run(() => {
          this.selectedPatientName.set('');
          this.cdr.markForCheck();
        });
        return;
      }

      // Set loading state immediately when person ID is selected
      this.ngZone.run(() => {
        this.selectedPatientName.set('Loading...');
        this.cdr.markForCheck();
      });

      // Poll for person data until it becomes available
      // Check more frequently initially, then slow down
      let attempts = 0;
      const maxAttempts = 100; // Increased to 5 seconds total (100 * 50ms)
      
      const checkForPersonData = () => {
        // Always run inside Angular zone to ensure change detection
        this.ngZone.run(() => {
          // Check if person ID changed (user selected different patient)
          if (this._selectedPersonId() !== personId) {
            return; // Stop checking if person ID changed
          }

          // Check if person data is available
          const personData = this.personService.get(personId);
          
          if (personData && personData.nameFullFormatted) {
            // Person data is loaded - update the name signal and trigger change detection
            this.selectedPatientName.set(personData.nameFullFormatted);
            this.cdr.detectChanges(); // Force immediate change detection
          } else if (attempts < maxAttempts) {
            // Data not yet available - check again after a delay
            attempts++;
            
            // Calculate delay - check more frequently initially
            const delay = attempts < 10 ? 25 : 50; // First 10 checks at 25ms, then 50ms
            
            // Schedule next check
            this.ngZone.runOutsideAngular(() => {
              setTimeout(() => {
                this.ngZone.run(() => {
                  checkForPersonData();
                  this.cdr.markForCheck(); // Mark for check on each poll attempt
                });
              }, delay);
            });
          } else {
            // Timeout reached - check one more time before showing fallback
            const finalCheck = this.personService.get(personId);
            if (finalCheck && finalCheck.nameFullFormatted) {
              this.selectedPatientName.set(finalCheck.nameFullFormatted);
            } else {
              // Only show Person ID if we truly can't find the data
              this.selectedPatientName.set(`Person ID: ${personId}`);
            }
            this.cdr.detectChanges(); // Force change detection
          }
        });
      };

      // Start checking immediately, then continue polling
      // Do first check synchronously, then continue with async polling
      this.ngZone.run(() => {
        checkForPersonData();
      });
    });

    /**
     * Initialize the segmented control with the current service-managed days filter value.
     */
    this.selectedDaysValue = this.priorAuthService.getCurrentDaysFilter();

    /**
     * Seed the subroute segmented control with the default incoming value to keep visuals in sync.
     */
    this.selectedSubrouteValue = this.subrouteOptions[0]?.value ?? 'incoming';
  }

  // Days filter options for the segmented control
  readonly daysOptions: { label: string; value: number | 'all' }[] = [
    { label: '30', value: 30 },
    { label: '60', value: 60 },
    { label: '90', value: 90 },
    { label: '180', value: 180 },
    { label: 'All', value: 'all' }
  ];

  // Subroute options for the segmented control
  readonly subrouteOptions: { label: string; value: string }[] = [
    { label: 'Incoming', value: 'incoming' },
    { label: 'Outgoing', value: 'outgoing' }
  ];

  // Current subroute selection (incoming/outgoing) - using signal for reactive updates
  private _currentSubroute = signal<string>('incoming');
  currentSubrouteIndex: number = 0; // Track the selected index for nz-segmented (0 = incoming, 1 = outgoing)
  selectedSubrouteValue: string = 'incoming';
  
  // Readonly accessor for current subroute
  get currentSubroute(): string {
    return this._currentSubroute();
  }

  // Predefined filter tabs from centralized configuration - using computed for reactive updates
  readonly predefinedFilters = computed((): PredefinedFilter[] => {
    // Only return filters if configuration has been loaded
    if (!this.columnConfig.configurationLoaded()) {
      return [];
    }

    // Check if using new version (custom worklists) or old version (predefined filters)
    const useNewVersion = this.userPreferences.useNewVersion();

    // If using new version, hide predefined filter tabs
    if (useNewVersion) {
      return [];
    }

    // If using old version, show predefined filter tabs from configuration
    const filters = this.columnConfig.getPredefinedFilters('prior-auth', this._currentSubroute());
    return filters;
  });

  // Predefined filters for count display in custom worklist view - using computed for reactive updates
  readonly predefinedFiltersForCounts = computed((): PredefinedFilter[] => {
    // Only return filters if configuration has been loaded
    if (!this.columnConfig.configurationLoaded()) {
      return [];
    }

    // Check if using new version (custom worklists)
    const useNewVersion = this.userPreferences.useNewVersion();

    // Only show counts in new version mode
    if (!useNewVersion) {
      return [];
    }

    // Get predefined filters for current subroute to show counts
    const filters = this.columnConfig.getPredefinedFilters('prior-auth', this._currentSubroute());
    return filters;
  });

  // Location dropdown options depend on subroute; expose label/value for UI - using computed for reactive updates
  readonly locationOptions = computed(() => {
    // Only return options if configuration has been loaded
    if (!this.columnConfig.configurationLoaded()) {
      return [];
    }
    return this.columnConfig
      .getLocationOptions('prior-auth', this._currentSubroute())
      .map(opt => ({ label: opt.label, value: opt.value }));
  });

  // Currently selected location value (config-driven); default to 'all'
  currentLocationValue: string = 'all';

  // Currently selected predefined filter - will be set after configuration loads
  selectedPredefinedFilter: PredefinedFilter | null = null;

  // Currently selected tab index for nz-tabset
  private _selectedTabIndex: number = 0;

  /**
   * Toggle state for showing only current user's assignments
   */
  showMyAssignmentsOnly: boolean = false;

  /**
   * Tracks the active EDOS days selection so the segmented control stays aligned with service state.
   */
  selectedDaysValue: number | 'all' = 30;

  // === Custom Worklist Properties ===

  /**
   * Currently selected custom worklist
   */
  selectedCustomWorklist: CustomWorklist | null = null;

  /**
   * Computed signal that returns custom worklists for current subroute
   */
  readonly customWorklists = computed(() => {
    return this.customWorklistService.getWorklistsForRoute('prior-auth', this._currentSubroute());
  });
  
  get selectedTabIndex(): number {
    return this._selectedTabIndex;
  }
  
  set selectedTabIndex(value: number) {
    this._selectedTabIndex = value;
  }
  
  ngOnInit(): void {
    // Configuration loading is handled by the effect in constructor
    // If configuration is already loaded, initialize immediately
    if (this.columnConfig.configurationLoaded() && !this.selectedPredefinedFilter) {
      this.initializeDefaultFilter();
    }
  }

  /**
   * Initialize the default filter once configuration is loaded
   */
  private initializeDefaultFilter(): void {
    // Check if using old version (predefined filters)
    const useNewVersion = this.userPreferences.useNewVersion();
    const currentSubrouteValue = this.currentSubroute;
    const optionIndex = this.subrouteOptions.findIndex(opt => opt.value === currentSubrouteValue);
    if (optionIndex >= 0) {
      this.currentSubrouteIndex = optionIndex;
      this.selectedSubrouteValue = currentSubrouteValue;
    }

    if (!useNewVersion) {
      // Old version: Set default predefined filter based on configuration
      if (this.columnConfig.configurationLoaded()) {
        const defaultFilter = this.columnConfig.getDefaultPredefinedFilter('prior-auth', this.currentSubroute);
        if (defaultFilter) {
          this.selectedPredefinedFilter = defaultFilter;
          this._selectedTabIndex = this.predefinedFilters().findIndex(f => f.id === defaultFilter.id);
          /**
           * Ensure the segmented control mirrors the default filter's days setting when configuration loads.
           */
          this.syncSelectedDaysWithFilter(defaultFilter);
          this.emitCombinedSelection(defaultFilter);
        }
      }
    } else {
      // New version: Check for default custom worklist and set it in the dropdown
      this.selectedPredefinedFilter = null;
      this._selectedTabIndex = 0;
      /**
       * Reset segmented control to the service's current days filter in custom worklist mode.
       */
      this.selectedDaysValue = this.priorAuthService.getCurrentDaysFilter();

      // Get default custom worklist for current route/subroute
      const defaultCustomWorklist = this.customWorklistService.defaultWorklistForRoute()('prior-auth', this.currentSubroute)
        || this.customWorklistService.defaultWorklistForRoute()('prior-auth');

      if (defaultCustomWorklist) {
        // Set the selected custom worklist to reflect in dropdown
        this.selectedCustomWorklist = defaultCustomWorklist;
      }
    }
  }

  /**
   * Handles subroute change (incoming/outgoing)
   * @param selection The selected subroute index or value depending on NG-ZORRO version
   */
  onSubrouteChange(selection: number | string | { value: string }): void {
    /**
     * Normalise nz-segmented output so both index-based and value-based payloads are accepted.
     * This keeps legacy behaviour intact after NG-ZORRO segmented control changes.
     */
    let resolvedIndex: number | null = null;
    let resolvedOption: { label: string; value: string } | undefined;

    if (typeof selection === 'object' && selection !== null && 'value' in selection) {
      resolvedOption = this.subrouteOptions.find(opt => opt.value === selection.value);
      resolvedIndex = resolvedOption ? this.subrouteOptions.indexOf(resolvedOption) : null;
    } else if (typeof selection === 'string') {
      resolvedOption = this.subrouteOptions.find(opt => opt.value === selection);
      resolvedIndex = resolvedOption ? this.subrouteOptions.indexOf(resolvedOption) : null;
    } else if (typeof selection === 'number') {
      resolvedIndex = selection;
      resolvedOption = this.subrouteOptions[selection];
    }

    if (resolvedIndex === null || !resolvedOption) {
      return;
    }

    this.currentSubrouteIndex = resolvedIndex;
    this.selectedSubrouteValue = resolvedOption.value;
    // Update the subroute signal - this will trigger reactive updates
    this._currentSubroute.set(resolvedOption.value);
    // Reset location selection when switching subroutes
    this.currentLocationValue = 'all';

    const useNewVersion = this.userPreferences.useNewVersion();

    if (!useNewVersion) {
      // Old version: Initialize default predefined filter for new subroute
      if (this.columnConfig.configurationLoaded()) {
        const defaultFilter = this.columnConfig.getDefaultPredefinedFilter('prior-auth', this.currentSubroute);
        if (defaultFilter) {
          this.selectedPredefinedFilter = defaultFilter;
          this._selectedTabIndex = this.predefinedFilters().findIndex(f => f.id === defaultFilter.id);
          this.syncSelectedDaysWithFilter(defaultFilter);
          this.emitCombinedSelection(defaultFilter);
        }
      }
    } else {
      // New version: Reset predefined selection and align segmented control with current service days filter
      this.selectedPredefinedFilter = null;
      this._selectedTabIndex = 0;
      this.selectedDaysValue = this.priorAuthService.getCurrentDaysFilter();

      // Get default custom worklist for current route/subroute
      const defaultCustomWorklist = this.customWorklistService.defaultWorklistForRoute()('prior-auth', this.currentSubroute)
        || this.customWorklistService.defaultWorklistForRoute()('prior-auth');

      if (defaultCustomWorklist) {
        // Set the selected custom worklist to reflect in dropdown
        this.selectedCustomWorklist = defaultCustomWorklist;
      }
    }
  }

  /**
   * Handles the change event from the days filter segmented control.
   * Supports both legacy index-based payloads and new value-based payloads introduced in recent NG-ZORRO releases.
   */
  onDaysFilterChange(selection: number | 'all' | { value: number | 'all' }): void {
    /**
     * Support both legacy (index-based) and current (value-based) nz-segmented outputs after NG-ZORRO upgrades.
     * When an incompatible payload is received we bail early to avoid emitting undefined filters.
     */
    let resolvedValue: number | 'all';

    if (typeof selection === 'object' && selection !== null && 'value' in selection) {
      resolvedValue = selection.value as number | 'all';
    } else if (this.daysOptions.some(option => option.value === selection)) {
      resolvedValue = selection as number | 'all';
    } else if (typeof selection === 'number' && selection >= 0 && selection < this.daysOptions.length) {
      resolvedValue = this.daysOptions[selection].value;
    } else {
      return;
    }

    // Update local state
    this.selectedDaysValue = resolvedValue;
    // Emit to parent component to update service
    this.daysFilterChange.emit(resolvedValue);
  }

  /**
   * Handles input from the global search box and emits the value.
   */
  onGlobalSearch(value: string): void {
    this.globalSearch.emit(value);
  }

  /**
   * Reset column widths to default
   */
  resetColumnWidths(): void {
    this.columnConfig.resetColumnWidths('prior-auth');
    this.columnWidthsReset.emit();
    // No page reload!
  }

  /**
   * Refresh the worklist data
   */
  onRefreshData(): void {
    // User clicked refresh button - emit event to trigger data reload in parent component
    this.refreshData.emit();
  }

  /**
   * Handles selection of a predefined filter tab
   * Clears user-applied column filters when switching between tabs
   */
  onPredefinedFilterSelect(filter: PredefinedFilter): void {
    // Store the previous filter to check if we're actually switching
    const previousFilter = this.selectedPredefinedFilter;

    // Update the selected filter
    this.selectedPredefinedFilter = filter;
    this._selectedTabIndex = this.predefinedFilters().findIndex(f => f.id === filter.id);

    /**
     * Sync the segmented control selection whenever a predefined filter supplies an explicit days constraint.
     */
    this.syncSelectedDaysWithFilter(filter);

    // Clear column filters when switching between predefined filter tabs
    // This ensures users see the expected predefined filter results
    if (previousFilter && previousFilter.id !== filter.id) {
      this.clearColumnFilters.emit();
    }

    // Emit the filter selection
    this.emitCombinedSelection(filter);
  }

  /**
   * Clears the active predefined filter
   */
  clearPredefinedFilter(): void {
    this.selectedPredefinedFilter = null;
    this.predefinedFilterSelect.emit(null);
    /**
     * Reset segmented control to the service's current days filter when clearing predefined criteria.
     */
    this.selectedDaysValue = this.priorAuthService.getCurrentDaysFilter();
  }

  /**
   * Handles the assignment filter toggle
   * Toggles between showing all assignments and only current user's assignments
   */
  onAssignmentToggle(): void {
    this.showMyAssignmentsOnly = !this.showMyAssignmentsOnly;
    this.assignmentToggle.emit(this.showMyAssignmentsOnly);
  }

  /**
   * Handles location dropdown change and re-emits the combined selection
   */
  onLocationChange(value: string): void {
    this.currentLocationValue = value;
    if (this.selectedPredefinedFilter) {
      this.emitCombinedSelection(this.selectedPredefinedFilter);
    }
  }

  /**
   * Helper to merge the active predefined filter with the current location filter
   * and emit a single PredefinedFilter object to consumers.
   */
  private emitCombinedSelection(baseFilter: PredefinedFilter): void {
    const combined = this.columnConfig.getCombinedCriteria(
      'prior-auth',
      this.currentSubroute,
      baseFilter,
      this.currentLocationValue
    );
    const merged: PredefinedFilter = { ...baseFilter, filters: combined };
    this.predefinedFilterSelect.emit(merged);
  }

  /**
   * Align the segmented control with the days filter declared by the selected predefined filter.
   */
  private syncSelectedDaysWithFilter(filter: PredefinedFilter | null): void {
    if (filter?.filters && filter.filters.daysFilter !== undefined) {
      this.selectedDaysValue = filter.filters.daysFilter;
    }
  }

  // === Custom Worklist Methods ===

  /**
   * Handle custom worklist selection from dropdown
   */
  onCustomWorklistSelect(worklistId: string | null): void {
    if (worklistId === null) {
      this.selectedCustomWorklist = null;
      this.selectedPredefinedFilter = null;
      return;
    }

    const worklist = this.customWorklistService.getWorklistById(worklistId);
    if (worklist) {
      this.selectedCustomWorklist = worklist;
      this.selectedPredefinedFilter = null;

      // Clear column filters when selecting a custom worklist
      // This ensures users see the expected custom worklist results without previously applied filters
      // Always clear filters to show server-side filtered data without user-applied filters
      this.clearColumnFilters.emit();

      // Convert custom worklist to predefined filter and emit
      const filter = this.customWorklistService.toPredefinedFilter(worklist);
      this.predefinedFilterSelect.emit(filter);
    }
  }

  /**
   * Open wizard to create new custom worklist
   */
  openCreateWorklistWizard(): void {
    this.showWizardView.emit({
      editWorklist: null,
      subroute: this.currentSubroute
    });
  }

  /**
   * Open wizard to edit existing custom worklist
   */
  openEditWorklistWizard(worklist: CustomWorklist): void {
    this.showWizardView.emit({
      editWorklist: worklist,
      subroute: this.currentSubroute
    });
  }

  /**
   * Delete a custom worklist
   */
  deleteCustomWorklist(worklist: CustomWorklist): void {
    this.customWorklistService.deleteWorklist(worklist.id);

    // If this was the selected worklist, clear selection
    if (this.selectedCustomWorklist?.id === worklist.id) {
      this.selectedCustomWorklist = null;
      this.initializeDefaultFilter();
    }
  }

  /**
   * Handle worklist saved from wizard
   * This method is called from the parent component after wizard saves
   */
  selectCustomWorklist(worklist: CustomWorklist): void {
    // Only select the worklist if it's marked as default
    if (worklist.isDefault) {
      this.selectedCustomWorklist = worklist;
      this.selectedPredefinedFilter = null;

      // Clear column filters when selecting a custom worklist
      // This ensures users see the expected custom worklist results without previously applied filters
      // Always clear filters to show server-side filtered data without user-applied filters
      this.clearColumnFilters.emit();

      // Convert to predefined filter and emit
      const filter = this.customWorklistService.toPredefinedFilter(worklist);
      this.predefinedFilterSelect.emit(filter);
    } else {
      // If not default, just clear any existing selection and let the default filter take over
      this.selectedCustomWorklist = null;
      this.initializeDefaultFilter();
    }
  }

  /**
   * Handle version toggle change
   * Switches between new version (custom worklist filtering) and old version (predefined filter bar)
   * @param useNew true to use new version, false to use old version
   */
  onVersionToggle(useNew: boolean): void {
    // Update user preference
    this.userPreferences.setUseNewVersion(useNew);

    if (useNew) {
      // Toggling TO new version: Clear everything and reset to initial state
      // User will need to select a custom worklist to load data
      this.selectedCustomWorklist = null;
      this.selectedPredefinedFilter = null;

      // Emit event to clear the data table
      this.clearData.emit();
    } else {
      // Toggling TO old version: Load all data for frontend filtering
      // Clear custom worklist selection
      this.selectedCustomWorklist = null;

      // Get and set the default predefined filter BEFORE refreshing data
      const defaultFilter = this.columnConfig.getDefaultPredefinedFilter('prior-auth', this.currentSubroute);
      if (defaultFilter) {
        this.selectedPredefinedFilter = defaultFilter;
        this._selectedTabIndex = this.predefinedFilters().findIndex(f => f.id === defaultFilter.id);
        // Emit the predefined filter selection to parent component
        this.predefinedFilterSelect.emit(defaultFilter);
      }

      // Trigger data refresh - filter is already set so it will be applied when data loads
      this.refreshData.emit();
    }
  }

  /**
   * Launch patient search dialog and handle selection
   */
  async onPatientSearch(): Promise<void> {
    try {
      const result = await this.cernerActionService.searchForPatientAndEncounter();

      if (result && result.personId > 0) {
        // Patient selected - store the selection
        this.selectedPatient = result;
        
        // Load person data using Clinical Office PersonService
        // load() initiates async data loading but doesn't return a promise
        this.personService.load({}, [{ personId: result.personId, encntrId: result.encounterId }]);
        
        // Give the service a moment to start loading before we begin polling
        // Set the person ID signal after a small delay to trigger the effect
        this.ngZone.runOutsideAngular(() => {
          setTimeout(() => {
            this.ngZone.run(() => {
              this._selectedPersonId.set(result.personId);
              this.cdr.markForCheck();
            });
          }, 100); // Small delay to let load() initiate
        });

        // Don't emit patientSelected here - wait for execute button
      } else {
        // Search was cancelled - clear the person ID signal
        this.ngZone.run(() => {
          this._selectedPersonId.set(null);
          this.cdr.markForCheck();
        });
      }
      // If result is null, search was cancelled - do nothing
    } catch (error) {
      // Error handled in CernerActionService
      this.ngZone.run(() => {
        this._selectedPersonId.set(null);
        this.selectedPatientName.set('Error loading patient');
        this.cdr.markForCheck();
      });
    }
  }

  /**
   * Execute patient search - apply filter and refresh worklist
   */
  onExecutePatientSearch(): void {
    if (this.selectedPatient) {
      // Clear the custom worklist dropdown before running the service
      this.selectedCustomWorklist = null;
      this.selectedPredefinedFilter = null;

      // Update the patient filter in the service
      this.priorAuthService.updatePatientFilter(this.selectedPatient.personId);

      // Emit to parent to trigger worklist refresh
      this.patientSelected.emit(this.selectedPatient);
    }
  }

  /**
   * Clear the patient filter
   */
  onClearPatientFilter(): void {
    this.selectedPatient = null;
    this._selectedPersonId.set(null);
    this.selectedPatientName.set('');
    this.priorAuthService.clearPatientFilter();
    this.clearPatientFilter.emit();
  }
}